.menu-wrapper{
    text-align: center;
    ul{
        display: inline-block;
        margin: 0;
        li{
            display: inline-block;
            list-style-type: none;
            margin: 0 10px;
            position: relative;
            a{
              text-transform: uppercase;
              padding: 40px 0;
            }
            a:hover{
                text-decoration: none;
            }
            ul.sub-menu{
            
              position: absolute;
              left: -18px;
              top: 233%;
              z-index: 1000;
              background: #fff;
              opacity: 0;
              visibility: hidden;
              box-shadow: 0 2px 4px #0000000d, 0 10px 30px #0000001a;
               padding: 11px  0;
               transition: 0.5s;
              li{
                display: block;
              
                a{
                
                    color: #000;
                    display: block;
                    padding: 10px 15px;
                    width: 174px;
                    text-align: left;
                
                }
              }
            }
        }

    }
}
